<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TYPE_NAME') }}</label>
          <vs-input class="w-full" v-model="lcType.typeName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TYPE_BEHAVIOUR') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="lcType.typeBehaviourEnumId">
            <vs-select-item v-for="(enumeration, index) in typeBehaviours" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
          <vs-input class="w-full" v-model="lcType.externalTypeId"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyLcType('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyLcType('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyLcType('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LcTypeService from '@/services/trade/LcTypeService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'lc-type-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      lcType: {},
      typeBehaviours: []
    }
  },
  methods: {
    async load () {
      let self = this
      LcTypeService.lcTypes({
        typeId: this.$route.params.id
      }).then(function (response) {
        self.lcType = response.data.lcTypeList[0] || {}
        self.lcType.disabled = self.lcType.disabled === 'Y'
      })

      EnumerationService.enumerations({
        enumTypeId: 'LcTypeBehaviour',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.typeBehaviours = response.data.enumerationList || []
      })
    },
    modifyLcType (nextAction) {
      let self = this
      LcTypeService.modifyLcType({
        typeId: this.$route.params.id,
        typeName: this.lcType.typeName,
        typeBehaviourEnumId: this.lcType.typeBehaviourEnumId,
        externalTypeId: this.lcType.externalTypeId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewLcType', params: { id: response.data.typeId } })
          } else {
            self.$router.push({ name: 'BrowseLcTypes' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
