import Endpoint from '@/services/Endpoint'

export default {
  lcTypes (payload) {
    return Endpoint().get('/s1/tradeFinance/lcType', {
      params: payload
    })
  },
  createLcType (payload) {
    return Endpoint().post('/s1/tradeFinance/lcType', payload)
  },
  modifyLcType (payload) {
    return Endpoint().patch('/s1/tradeFinance/lcType', payload)
  }
}
